.nav {
    position: absolute;
    height: 92px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    z-index: 9999;
    &__menu {
        display: flex;
        align-items: center;
        gap: 48px;
        &__button{
            padding: 5px 28px 5px 28px;
            border: 1px solid #F5F3F1;
            border-radius: 20px;
            transition: 0.3s ease-out;
            &:hover{
                color: #3E3025;
                background-color: #F5F3F1;
            }
        }
        a {
            @include mulish500;
            font-size: 16px;
            line-height: 30px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: #F5F3F1;
        }
        
    }
}