@font-face {
    font-family: 'Tenor-Sans';
    font-style: normal;
    src: url('./fonts/MyFont-Regular.ttf');
}

@font-face {
    font-family: 'Mulish-Regular';
    font-style: normal;
    src: url('./fonts/Static/Mulish-Regular.ttf');
}

@font-face {
    font-family: 'Mulish-Medium';
    font-style: normal;
    src: url('./fonts/Static/Mulish-Medium.ttf');
}

@font-face {
    font-family: 'Mulish-SemiBold';
    font-style: normal;
    src: url('./fonts/Static/Mulish-Medium.ttf');
}

@mixin tenor{
    font-family: 'Tenor-Sans';
    font-weight: 400;
}

@mixin mulish400{
    font-family: 'Mulish-Regular';
    font-weight: 400;
}

@mixin mulish500{
    font-family: 'Mulish-Medium';
    font-weight: 500;
}
