.hero {
    height: 100vh;
    width: 100%;
    position: relative;

    &__carousel {
        height: 100%;
        width: 100%;
        position: absolute;
        z-index: 1;

        &__filter {
            height: 100%;
            width: 100%;
            background: linear-gradient(0deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.186584) 32.15%, rgba(0, 0, 0, 0) 70.77%), rgba(0, 0, 0, 0.4);
            background-blend-mode: multiply;
            position: absolute;
            z-index: 2;
        }

        img {
            position: relative;
            height: 100%;
            width: 100%;
            z-index: 1;
        }
    }

    &__content {
        height: 100%;
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        p {
            @include mulish500();
            font-size: 16px;
            line-height: 36px;
            letter-spacing: 0.08em;
            text-transform: uppercase;
            color: #F5F3F1;
        }

        h1 {
            @include tenor();
            font-size: px_to_vw_desktop(120);
            line-height: 1.056em;
            letter-spacing: -0.02em;
            text-transform: uppercase;
            color: #F5F3F1;
        }

        &__text {
            gap: 16px;
        }

        &__controls {
            display: flex;
            justify-content: space-between;
            width: 100%;
            margin-top: px_to_vw_desktop(40);
            padding: 16px 0 16px 0;
            border-top: 1px solid #908F8B1E;
            &__description {
                display: flex;
                gap: 32px;
                &__number {}

                &__text {}
            }

            &__arrows {
                display: flex;
            }
        }
    }
}